'use client';

import Link from 'next/link';
import styles from './unauthorized.module.css';

export default function Unauthorized() {
    return (
        <div className={styles.container}>
            <div className={styles.glow} />

            <main className={styles.main}>
                <div className={styles.content}>
                    <div className={styles.iconWrapper}>
                        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" className={styles.icon}>
                            <circle cx="12" cy="12" r="10" />
                            <path d="M12 8v4M12 16h.01" />
                        </svg>
                    </div>

                    <h1 className={styles.title}>Access Denied</h1>

                    <p className={styles.description}>
                        Your account is not authorized to access this service.
                        Please contact an administrator to request access.
                    </p>

                    <div className={styles.actions}>
                        <Link href="/" className={styles.btnSecondary}>
                            Return Home
                        </Link>
                        <button
                            onClick={() => fetch('/api/auth/logout', { method: 'POST' }).then(() => window.location.href = '/')}
                            className={styles.btnDanger}
                        >
                            Logout
                        </button>
                    </div>

                    <div className={styles.info}>
                        <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                            <circle cx="12" cy="12" r="10" />
                            <path d="M12 16v-4M12 8h.01" />
                        </svg>
                        <span>If you believe this is an error, please reach out to the administrator with your Discord username.</span>
                    </div>
                </div>
            </main>
        </div>
    );
}
